.app {
  padding: 20px;
  border-radius: 5px;
  height: 100%; }

.app .app-content {
  padding: 20px 0 0 0;
  height: 400px;
  overflow-x: hidden;
  overflow-y: auto; }

.app [class^="flaticon-"]:before,
.app [class*=" flaticon-"]:before,
.app [class^="flaticon-"]:after,
.app [class*=" flaticon-"]:after {
  margin: 0; }

.nav-bar {
  text-align: center; }
  .nav-bar ul {
    display: flex;
    justify-content: center;
    align-items: center; }
    .nav-bar ul li {
      list-style-type: none;
      display: inline-block;
      padding: 5px; }
      .nav-bar ul li img {
        height: 30px;
        width: 30px;
        margin-left: 10px; }
    .nav-bar ul a:focus {
      outline: none; }

.user-details {
  padding: 20px;
  margin: 0 0 20px 0;
  text-align: center; }

.user-name {
  margin: 0 0 20px 0;
  font-size: 22px; }

.user-coins-count {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center; }

.user-coins-rate {
  margin: 10px 0 0 0;
  display: flex;
  align-items: center; }

.user-coins-count img, .user-coins-rate img {
  margin: 0 5px 0 0; }

.chart {
  margin: 20px 0;
}

.chart-name {
  margin: 5px 0;
}

.chart-desc {
  font-size: 0.8rem;
}
.statistic-chart:not(:last-child) {
  margin: 0 0 20px 0;
  border-bottom: 1px solid #f4f5f7;
}
.filter {
  text-align: center;
  position: relative; }
  .filter input {
    max-height: 100%;
    width: 100%;
    border-radius: 3px;
    padding: 5px; }

.contact-preview {
  display: inline-block;
  padding: 0 25px;
  height: 50px;
  width: 240px;
  font-size: 17px;
  line-height: 50px;
  border-radius: 25px;
  background-color: #f1f1f1; 
}
  
.contact-preview img {
  float: left;
  margin: 0 10px 0 -25px;
  height: 50px;
  width: 50px;
  border-radius: 50%; 
}

.contacts-list-item {
  margin: 10px 0; 
}

.contacts-page {
  position: relative;
  height: 100%; }
  .contacts-page .action-container {
    position: absolute;
    bottom: -3px;
    right: 10px; }
  .contacts-page .contacts-container {
    max-height: 75vh;
    overflow-x: auto;
    margin: 10px;
    position: relative; }
    .contacts-page .contacts-container .add-link {
      position: fixed;
      bottom: 50px;
      right: 50px;
      border-radius: 50px; }

.contact-details-header {
  display: flex;
  justify-content: space-between;
  height: 40px; }

.contact-details-header a:not(:last-child) {
  margin: 0 10px 0 0; }

.contact-details {
  position: relative; }
  .contact-details .contact-details-row {
    margin: 10px 0 0 0; }
  .contact-details .edit-link {
    position: absolute;
    right: 20px;
    top: 20px; }
    .contact-details .edit-link img {
      height: 30px;
      width: 30px; }

.moves-list-container, .transter-coins-container {
  margin: 20px 0; }

.container-Transfer {
  margin: 10px; }
  .container-Transfer form {
    width: 100%;
    display: flex; }
    .container-Transfer form input {
      width: 100%;
      padding: 5px;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px; }
    .container-Transfer form button {
      background-color: #b5dbfe;
      font-size: 1rem;
      padding: 5px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      color: white; }
    .container-Transfer form *:focus {
      outline: none; }

.moves-list {
  margin: 10px 0; }
  .moves-list h1 {
    text-align: center; }
  .moves-list li {
    margin: 5px;
    border-bottom: 2px double; }
    .moves-list li p {
      font-size: 1rem;
      padding: 0;
      margin: 0; }
      .moves-list li p span {
        font-weight: bold;
        margin-right: 5px;
        font-size: 18px; }

.contact-details-header {
  display: flex;
  justify-content: space-between;
  height: 40px; }

.contact-details-header a:not(:last-child) {
  margin: 0 10px 0 0; }

.contact-edit .contact-edit-row {
  margin: 10px 0 0 0; }
  .contact-edit .contact-edit-row input {
    margin-left: 10px;
    padding: 5px;
    border-radius: 3px; }
  .contact-edit .contact-edit-row span {
    display: block;
    margin-left: 10px;
    margin-bottom: 5px; }

.contact-edit .btn-container {
  display: flex;
  justify-content: space-between;
  top: 0; }
  .contact-edit .btn-container button {
    background: none;
    border: none; }
    .contact-edit .btn-container button:focus {
      outline: none; }
    .contact-edit .btn-container button img {
      height: 30px;
      width: 30px; }

.contact-edit .save-btn {
  margin-top: 10px;
  display: block;
  margin: 10px auto;
  padding: 5px 10px;
  background-color: #8bb7f0;
  cursor: pointer;
  border-radius: 5px;
  font-size: 20px;
  color: white; }

.moves-list-container, .transter-coins-container {
  margin: 20px 0; }

.signup-page {
  text-align: center; }
  .signup-page label {
    text-align: center;
    display: block; }

html {
  height: 100%; }

body {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #fabd10b5;
  color: #0f0a0a;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif; }

a, a:visited, a:hover, a:active {
  text-decoration: none;
  color: inherit; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

